body {
    background: $bege-claro;
    border: 10px solid $bege-medio;
    color: $cinza;
    @include device(s){
        border: none;
    }
}


img {
    display: block;
    max-width: 100%;
}

p {
    @include tipo-1(18);
}

.titulo {
    @include tipo-1(18);
    text-transform: uppercase;
    @include device(s){
        margin-bottom: 40px;
    }
    &::after{
        content: '';
        display: block;
        width: 75%;
        float: right;
        height: 2px;
        background: $cinza;
        margin-top: 14px;
        @include device(m){
            width: 60%;
        }
    }
}