footer {
    padding: 20px;
    background: $cinza;
    @include box-shadow(0 0 0 10px $cinza);
    p{
        color: $bege-claro;
        @include device(s){
            text-align: center;
        }
    }
    a img {
        float: right;
        @include device(s){
            float: none;
            margin: 20px auto;
        }
    }
}